import wzorE from '../../assets/img/imgWzorE.jpg';

export const WzorE = ()=>(
    <div className='wzorE'>
        <h4>Przykłady:</h4>
    <div className='wzorE1Head'>
        <h4>Zadanie A - opis fotografii</h4>
        <p>Zadanie polega na opisaniu fotografii (należy opisać ludzii przedmioty,które się na niej znajdują, a następnie omówić sytuację, którą przedstawia).</p>
        <p>Przydatne zwroty i wyrażenia</p>
        <div>
            <p>Na pierwszym planie, na drugim planie, obok, wokół, w tle, w głębi</p>
            <p>Na fotografii (zdjęciu, obrazku, ilustracji) widzę...</p>
            <p>Myślę,że... / Sądzę, że... / Wydaje mi się, że...</p>
        </div>
    </div>
    <div className="zad1">
        <h5>Proszę opisać fotografię i przedstawioną na niej sytuację.</h5>
        <img src={wzorE} alt=''></img>
        <p>Na pierwszym planie widzę małą dziewczynkę, która jedzie ulicą. Ma sześć, może siedem lat i uczy się jeździć na rolkach. Ma na sobie biało-niebieską bluzkę, białe spodenki, na kolanach i łokciach różowe ochraniacze, a na głowie różowy kask. Na drugim planie jedzie na rowerze mężczyzna w średnim wieku. To chybajej tata. Ma na sobie szare szorty i czarną koszulkę.Jest uśmiechnięty i cały czas obserwuje, co robi córka. Obok mężczyzny, po lewejstronie,jedzie jeszcze jedna dziewczynka. Możeto starsza córka? Jest szczupła i ma długie, jasne włosy. Ona dobrze jeździ na rowerze. Myślę, że ta rodzina lubi uprawiać sport. Dziś już długo jeździli i teraz są trochę zmęczeni,ale zadowoleni, ponieważ młodsza córka corazlepiej jeździ na rolkach. Sądzę, że tata mobilizuje córki do tego, by aktywnie spędzały wolny czas. W ten sposób dbają o kondycję oraz zdrowie.</p>
        <p>Wydaje mi się, że jest lato i są wakacje, ponieważ pogodajest słoneczna,jest ciepło, a drzewai trawa w tle są zielone. Sądzę, że ta rodzina ciekawie zaplanowała urlop. Fotografia bardzo misię podoba, bo przypomina moje wakacjei czas, który spędzam z rodziną. Chętnie znów pojechałabym na takie wakacje.</p>
    </div>

    <div className='wzorE2Head'>
        <h4>Zadanie B - monolog (wypowiedź na podany temat)</h4>
        <p>Należy umieć wyrazić opinię na podany temat, streścićfilm lub książkę albo zrelacjonować wydarzenie z przeszłości. Monolog najczęściej dotyczy własnych zainteresowań, doświadczeń i planów.</p>
        <p>Przydatne zwroty i wyrażenia</p>
        <div>
            <p>Myślę, że... Sądzę, że... Uważam, że... Moim zdaniem... Z jednej strony... Z drugiej strony... Z kolei...</p>
        </div>
    </div>
    <div className="zad2">
        <h5>Proszę wypowiedzieć się na podany temat.</h5>
        <h5>Jak spędza Pan/Pani wolny czas? Jakie formy wypoczynku uważa Pan/Paniza najlepsze? (monolog) </h5>
        <p>Kiedy mam wolnyczas, najchętniej spotykam się z przyjaciółmi i znajomymi. Zwykle idziemy razem do kawiarni albo do kina. Wspólnie decydujemy, co mamy ochotę robić. Dużo się śmiejemyi robimy wszystko, żeby się dobrze bawić.</p>
        <p>Lubię też w wolnymczasie czytać książki. Nie książki poważne,ale takie, które się dobrze czyta — przygodowealbo kryminały Agathy Christie.</p>
        <p>Niektórzy uważają, że tylko aktywny wypoczynek jest dobry, bo jest korzystny dla naszego zdrowiai zalecany jest wszystkim, w każdym wieku. Myślę, że szczególnie jest potrzebny tym, którzy cały dzień w pracy spędzają przed komputerem.</p>
        <p>Z jednej strony ważnejest, aby dbać o swoje zdrowie,ale z drugiej strony przecież nie każdy lubi chodzić na basen czy uprawiać jakiś sport. Są osoby, które wolą po całym dniu pracy usiąść przed telewizorem, obejrzećjakiś film, ewentualnie pójść na krótki spacer do, parku. Z kolei młodzi ludzie chętnie w wolnymczasie korzystają z komputera — Internetu, gier komputerowych. Niejest dobrze, jeśli przed komputerem spędzasię za dużo czasu, więc rodzice powinni zadbać, aby komputer nie był włączony cały wieczór i noc.</p>
        <p>Moim zdaniem najlepiej w wolnym czasie robić to, co się lubi. Sądzę, że najważniejsze jest, aby zrelaksowaćsię i odpocząć. Wolny czas trzeba spędzić w jakiś przyjemnydla siebie sposób.</p>
    </div>

    <div className='wzorE3Head'>
        <h4>Zadanie C - sytuacja komunikacyjna (dialog z egzaminatorem)</h4>
        <p>Trzecie zadanie to dialog z egzaminatorem. Tematyka związana jest z typowymi sytuacjami (oficjalnymii nieoficjalnymi) z życia codziennego, np. rozmowatelefoniczna. Kiedyto konieczne, należy pamiętać o zachowaniu form grzecznościowych(np. pan/pani). Zdający najczęściej wchodzi w rolę: przyjaciela, znajomego, członka rodziny, turysty, obcego, ucznia, nauczyciela, studenta, profesora, pracownika, pracodawcy, pacjenta czy klienta.</p>
        <p>Czas przed rozpoczęciem wypowiedzi warto przeznaczyć na szybkie zastanowienie się, o co należy zapytać oraz jaką rolę trzeba odegrać — wszystkie te informacje zdający odnajdzie w poleceniu. Ważne jest więc, aby uważnie przeczytać polecenie i zaplanować, jak pokierować rozmową, jakich użyć argumentów. Nie warto pisać całego dialogu, ponieważnie jesteśmy w stanie przewidzieć reakcji naszego rozmówcy. Najlepiej więc przygotować plan ogólny.</p>
        <p>Niektóre zwroty i wyrażenia używane w typowych sytuacjach komunikacyjnych</p>
        <div>
            <table>
                <thead>
                    <tr>
                        <td><b>Oficjalnie</b></td>
                        <td><b>Nieoficjalnie</b></td>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td colSpan={2}><b>Powitanie/pożegnanie:</b></td>
                    </tr>
                    <tr>
                        <td>Dzień dobry/Dobry wieczór</td>
                        <td>Cześć / Hej</td>
                    </tr>
                    <tr>
                        <td>Witam pana/panią</td>
                        <td>Witaj</td>
                    </tr>
                    <tr>
                        <td>Witam serdecznie</td>
                        <td>Miło cię widzieć</td>
                    </tr>
                    <tr>
                        <td>Do zobaczenia / Do widzenia</td>
                        <td>Cześć / Trzymajsię / Na razie</td>
                    </tr>
                    <tr>
                        <td colSpan={2}><b>Przedstawianiesiebie lub kogoś:</b></td>
                    </tr>
                    <tr>
                        <td>Pozwoli pan/pani, że się przedstawię, nazywam się Jan Nowak.</td>
                        <td>Jestem Jan Nowak. Mam na imię Jan.</td>
                    </tr>
                    <tr>
                        <td>Przepraszam, chciałabym panu/pani przedstawić mojego męża...</td>
                        <td>Poznajcie się, to mój mąż...</td>
                    </tr>
                    <tr>
                        <td>Mam zaszczyt przedstawić państwu...</td>
                        <td>To mój kolega...</td>
                    </tr>
                    <tr>
                        <td colSpan={2}><b>Pytania i odpowiedzi grzecznościowe:</b></td>
                    </tr>
                    <tr>
                        <td>Jak się pan/pani ma? Dziękuję, wszystko dobrze.</td>
                        <td>Jak się masz? Świetnie!</td>
                    </tr>
                    <tr>
                        <td>Co u państwa nowego? Dziękujemy, wszystko tak, jak było.</td>
                        <td>Co u ciebie? Nic nowego.</td>
                    </tr>
                    <tr>
                        <td>Co u pana/pani słychać? Dziękuję, wszystko w porządku.</td>
                        <td>Co słychać? Po staremu.</td>
                    </tr>
                    <tr>
                        <td colSpan={2}><b>Zwracaniesię do odbiorcy:</b></td>
                    </tr>
                    <tr>
                        <td>Proszę pana/pani.</td>
                        <td>Tomku! Kasiu! Słuchaj! Wiesz co...</td>
                    </tr>
                    <tr>
                        <td>Przepraszam, czy mogę przeszkodzić?</td>
                        <td>Można na moment?</td>
                    </tr>
                    <tr>
                        <td colSpan={2}><b>Wprowadzanie pytania:</b></td>
                    </tr>
                    <tr>
                        <td>Proszę mi powiedzieć...</td>
                        <td>Powiedz mi...</td>
                    </tr>
                    <tr>
                        <td>Czy może mi pan/pani powiedzieć...</td>
                        <td>Czy możesz mi powiedzieć...</td>
                    </tr>
                    <tr>
                        <td>Proszę pana/pani, czy...</td>
                        <td>Słuchaj, czy wiesz...</td>
                    </tr>
                    <tr>
                        <td colSpan={2}><b>Odpowiedzina pytania:</b></td>
                    </tr>
                    <tr>
                        <td>Przykro mi, ale nie wiem.</td>
                        <td>Nie wiem. / Nie mam pojęcia.</td>
                    </tr>
                    <tr>
                        <td colSpan={2}><b>Przepraszanie:</b></td>
                    </tr>
                    <tr>
                        <td>Przepraszam pana, panie dyrektorze.</td>
                        <td>Bardzo przepraszam.</td>
                    </tr>
                    <tr>
                        <td>Przepraszam i obiecuję, że to się już nigdy nie powtórzy.</td>
                        <td>Nie gniewaj się! Wybacz!</td>
                    </tr>
                    <tr>
                        <td colSpan={2}><b>Niezadowolenie:</b></td>
                    </tr>
                    <tr>
                        <td>Jestem niezadowolony/niezadowolona z...</td>
                        <td>Jestem zły/zła! Jestem wściekły/wściekta!</td>
                    </tr>
                    <tr>
                        <td colSpan={2}><b>Propozycje i odpowiedzi:</b></td>
                    </tr>
                    <tr>
                        <td>Mam propozycję dla państwa/pani/pana...</td>
                        <td>Masz ochotę na...?</td>
                    </tr>
                    <tr>
                        <td>Świetnie, znakomity pomyst!</td>
                        <td>Dlaczego nie! / No pewnie, że tak!</td>
                    </tr>
                    <tr>
                        <td>Niestety, dziś to niemożliwe. Czy mógtby to pan przełożyć?</td>
                        <td>Chyba nie. / Nie mam ochoty. / Mowy nie ma.</td>
                    </tr>
                    <tr>
                        <td colSpan={2}><b>Gratulacje i życzenia:</b></td>
                    </tr>
                    <tr>
                        <td>Składam panu/pani najserdeczniejsze gratulacje z okazji...</td>
                        <td>Moje gratulacje!</td>
                    </tr>
                    <tr>
                        <td>Z okazji imienin / urodzin życzę panu/pani spełnienia wszystkich marzeń.</td>
                        <td>Zdrowia, szczęścia, miłości...</td>
                    </tr>
                    <tr>
                        <td colSpan={2}><b>Zaproszenia i podziękowania:</b></td>
                    </tr>
                    <tr>
                        <td>Mam zaszczyt zaprosić pana/panią/państwa...</td>
                        <td>Naprawdę serdecznie was zapraszam...</td>
                    </tr>
                    <tr>
                        <td>Dziękujemy za zaproszenie.</td>
                        <td>Z przyjemnością przyjdziemy!</td>
                    </tr>
                    <tr>
                        <td>Niestety nie możemy przyjść z powodu...</td>
                        <td>Dziękujemy za zaproszenie, ale...</td>
                    </tr>
                    <tr>
                        <td colSpan={2}><b>Perswazja i argumentacja:</b></td>
                    </tr>
                    <tr>
                        <td>Powinien pan/powinna pani...</td>
                        <td>Powinieneś/powinnaś...</td>
                    </tr>
                    <tr>
                        <td>Wydaje mi się, że powinien pan/powinna pani...</td>
                        <td>Sądzę, że powinieneś/powinnaś...</td>
                    </tr>
                </tbody>
            </table>
        </div>
    </div>
    <div className="zad3">
        <h5>Proszę wykonać następujące zadanie.</h5>
        <h5>Idzie Pan/Pani ulicą ze swoją siostrą i spotyka nauczycielkę języka polskiego, która pomagała Panu/Pani przygotować się do egzaminu. Proszę rozpocząć rozmowę, przedstawić siostrę i powiedzieć o zdanym egzaminie.</h5>
        <p><b>Student: </b>Dzień dobry, czy poznaje mnie pani?</p>
        <p><b>Nauczycielka: </b>Oczywiście, że tak! Dzień dobry John! Jak mogłabym zapomnieć mojego najlepszego studenta?</p>
        <p><b>Student: </b>Dziękuję, chciałbym pani przedstawić moją siostrę Annę.</p>
        <p><b>Nauczycielka: </b>Miło mi poznać. John jest naprawdę zdolnym studentem. Cou ciebie słychać, John?</p>
        <p><b>Student: </b>Wszystko w porządku,a najlepsza wiadomośćjest taka, że zdałem egzamin!</p>
        <p><b>Nauczycielka: </b>Gratuluję! Wiedziałam, że nie może być inaczej! Czy był trudny?</p>
        <p><b>Student: </b>Tak, bardzo, ale wszystko skończyło się dobrze.</p>
        <p><b>Nauczycielka: </b>A czy wiesz, jak poszło Andreasowi z naszej grupy?</p>
        <p><b>Student: </b>Nie mam pojęcia, nie kontaktował się ze mną.</p>
        <p><b>Nauczycielka: </b>A jakie masz teraz plany?</p>
        <p><b>Student: </b>Przyjechałem z siostrą na wakacje, żeby jej pokazać Polskę, a potem wracam do Irlandii i zaczynam pracę.</p>
        <p><b>Nauczycielka: </b>Życzę więc powodzeniai pisz czasami do mnie.</p>
        <p><b>Student: </b>Oczywiście, obiecuję. A może pójdziemy razem na kawę?</p>
        <p><b>Nauczycielka: </b>Bardzo chętnie,ale za pół godziny mam zajęcia..., może innym razem.</p>
        <p><b>Student: </b>Dobrze.</p>
        <p><b>Nauczycielka: </b>Do widzenia.</p>
        <p><b>Student: </b>Do widzenia.</p>
    </div>
</div>
)